body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* overflow: hidden; */
  background-color: rgb(66,53,57);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  border-radius: 50px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #423539; 
  /* border-radius: 50px; */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #E8945F; 
}
.side-nav {
  height: 94vh;
  background-color: rgb(0, 0, 0);
  background-color: rgb(66, 53, 57, 0.2);
}

.side-nav a {
  color: white;
}

.side-nav-item {
  padding: 15px;
  padding-left: 20px;

  -webkit-transition: background-color 1s ease-out;
  transition: background-color 1s ease-out;

  -webkit-transition: padding-left 0.5s ease-out;
  transition: padding-left 0.5s ease-out;
}

.side-nav-item-active {
  padding: 15px;
  padding-left: 40px;
  background-color: #E8945F;

  -webkit-transition: background-color 1s ease-out;
  transition: background-color 1s ease-out;

  -webkit-transition: padding-left 0.3s ease-out;
  transition: padding-left 0.3s ease-out;
}

.side-nav-col-1 {
  padding-left: 0rem;
}

.side-nav-col-2 {
  padding-right: 0rem;
}

.top-nav-bar {
  height: 6vh;
  background-color: rgb(0,0,0);
  background-color: rgb(66, 53, 57, 0.7);
}
.top-nav-bar span {
  color: #E8945F;
}

.nav-bar-brand {
  padding: 0 100px;
}

.bg-image {
  background-image: url(/react-weibo/static/media/bg.a5babdcd.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  filter: blur(35px);
  -webkit-filter: blur(35px);

  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  min-height: 100vh;
}

Layout {
  height: 100%;
}
